footer{
    width: 100%;
    height: 300px;
    margin-top: 100px;
    background: #00467a;
    .navbar {
        width: 100%;
        padding: 30px 0px;
    
        margin-top: 30px;
        .logo {
          display: block;
          text-decoration: none;
          color: white;
          font-size: 25px;
          font-weight: 700;
          padding-bottom: 10px;
          padding-top: 10px;
          &:hover {
            color: #f49d37;
          }
        }
        .nav {
          display: flex;
          align-items: center;
          .md-block({display : none});
    
          li {
            margin: 0px 10px;
            .link {
              text-decoration: none;
              font-size: 18px;
              font-weight: 700;
              color: white;
              transition: all 100ms;
              display: block;
              padding: 10px 0px;
              overflow: hidden;
              position: relative;
              .md-block({
                 font-size: 14px; 
              });
              &:hover {
                color: #f49d37;
              }
              &::after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                transform: translate(-3000px);
                display: block;
                width: 50%;
                height: 3px;
                background: #f49d37;
              }
              &::before {
                content: "";
                display: block;
                position: absolute;
                bottom: 0;
                transform: translate(3000px);
                right: 0;
                width: 50%;
                height: 3px;
                background: #f49d37;
              }
            }
            &:hover .link::after,
            &:hover .link::before {
              transform: translate(0px);
              transition: transform 400ms;
              color: #f49d37;
            }
          }
        }
    }
}