header {
  width: 100%;
  .navbar {
    width: 100%;
    padding: 30px 0px;
    background: #00467a;
    margin-top: 30px;
    .logo {
      display: block;
      text-decoration: none;
      color: white;
      font-size: 25px;
      font-weight: 700;
      padding-bottom: 10px;
      padding-top: 10px;
      &:hover {
        color: #f49d37;
      }
    }
    .nav {
      display: flex;
      align-items: center;
      .md-block({display : none});

      li {
        margin: 0px 10px;
        .link {
          text-decoration: none;
          font-size: 18px;
          font-weight: 700;
          color: white;
          transition: all 100ms;
          display: block;
          padding: 10px 0px;
          overflow: hidden;
          position: relative;
          .md-block({
             font-size: 14px; 
          });
          &:hover {
            color: #f49d37;
          }
          &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            transform: translate(-3000px);
            display: block;
            width: 50%;
            height: 3px;
            background: #f49d37;
          }
          &::before {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            transform: translate(3000px);
            right: 0;
            width: 50%;
            height: 3px;
            background: #f49d37;
          }
        }
        &:hover .link::after,
        &:hover .link::before {
          transform: translate(0px);
          transition: transform 400ms;
          color: #f49d37;
        }
      }
    }
    .close {
      display: none;
      font-size: 20px;
      font-weight: 700;
      padding: 5px 10px;
      .md-block({
            display: block;
            font-size: 14px;
            padding: 5px;
        });
    }
  }
  .search {
    margin: 50px 0px;
    text-align: center;
    padding: 25px 0px;
    // background: #fff;
    input {
      width: 70%;
      padding: 10px;
      background: transparent;
      border: 3px solid #00467a;
      border-radius: 5px;
      font-size: 16px;
      font-weight: 700;
      color: black;
    }
    .searching {
      border: 3px solid #00467a;
      border-radius: 5px;
      font-size: 16px;
      font-weight: 700;
      color: black;
      padding: 10px;
    }
  }
  .shopBag {
    text-align: right;
    position: relative;
    a {
      position: fixed;
      right: 0px;
      font-size: 18px;
      color: #00467a;
      border: 3px solid #00467a;
      display: flex;
      flex-direction: column;
      padding: 30px 20px;
      border-top-left-radius: 15px;
      border-bottom-left-radius: 15px;
      border-right: none;
      text-decoration: none;
      box-shadow: 10px 10px 10px black;
      z-index: 11;
      span {
        position: absolute;
        top: 13px;
        right: 6px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        font-weight: 700;
        width: 20px;
        height: 20px;
        text-align: center;
        border-radius: 50%;
        color: white;
        background: #00467a;
      }
    }
  }

  .scroll-top {
    position: fixed;
    top: 600px;
    right: 100px;
    background: #00467a;
    padding: 10px;
    border-radius: 50%;
    display: none;
    a {
      color: white;
      text-decoration: none;
      font-size: 14px;
      font-weight: 700;
    }
  }

 
  .owl-carousel {
    .owl-stage-outer {
      .owl-stage {
        .owl-item {
          .hr_item {
            position: relative;
            .hr-item-text {
              position: absolute;
              width: 30%;
              height: 30%;
              background: rgba(0, 0, 0, 0.4);
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              padding: 30px;
              border: 6px solid #00467a;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              h2 {
                font-size: 30px;
                font-weight: 700;
                color: white;
                text-align: center;
                font-family: "etelkBold";
                margin-bottom: 20px;
              }
              .tel-carousel{
                display: inline-block;
                text-align: center;
                margin: auto;
                text-decoration: none;
                font-size: 25px;
                font-weight: 100;
                font-family: "etelkLight";
                color: #00467a;
                background: #f49d37;
                padding: 15px 25px;
                border-radius: 30px;
                transition: all 500ms;
                &:hover{
                  color: #00467a;
                  transform: scale(1.1);
                }
              }
            }
          }
        }
      }
    }
    .owl-nav{
       button{
        background: transparent; 
        border: none;
        font-size: 60px;
        font-weight: 700;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #f49d37;
      }
      .owl-prev{
        position: absolute;
        top: 50%;
        left: 80px;
        transform: translateY(-50%);
      }
      .owl-next{
        position: absolute;
        top: 50%;
        right: 80px;
        transform: translateY(-50%);
      }
    }
    .owl-dots{
      position: absolute;
      bottom: 10px;
      right: 43%;
      transform: translate(-50% , -50%);
      .owl-dot {
        width: 20px;
        height: 20px;
        background: #fff;
        border: 2px solid #f49d37;
        margin: 0px 10px;
        border-radius: 50%;
      }
      .owl-dot.active{
        background: #f49d37;
      }
    }
    img {
      width: 100%;
      height: 800px;
    }
  }
}
