section {
  width: 100%;
  .about {
    margin-top: 100px;
    .about_text {
      display: flex;
      align-items: center;
      .img_logo {
        width: 40%;
      }
      .text {
        width: 60%;
        p {
          font-size: 14px;
          font-weight: 400;
          line-height: 30px;
          text-align: justify;
        }
      }
    }
  }
  .product {
    margin-top: 100px;
    .owl-carousel {
      margin-top: 50px;
      .owl-dot {
        display: none;
      }
      .owl-nav {
        button {
          background: transparent;
          border: none;
          font-size: 70px;
          font-weight: 700;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #f49d37;
        }
        .owl-prev {
          position: absolute;
          top: 20%;
          left: -15%;
        }
        .owl-next {
          position: absolute;
          top: 20%;
          right: -15%;
        }
      }
      .img_product {
        max-width: 200px;
        height: 300px;
      }
    }
  }
  .benefits {
    margin-top: 100px;
    .text {
      text-align: center;
      margin: 50px 0px;
      .img-icon {
        max-width: 100px;
        height: 100px;
        margin: auto;
      }
      h3 {
        font-size: 22px;
        font-weight: 700;
        margin: 20px 0px;
        text-align: left;
        font-family: "etelkBold";
        color: #00467a;
        &::after {
          content: "";
          display: block;
          margin-top: 10px;
          width: 20%;
          height: 4px;
          background: #00467a;
        }
      }
      p {
        font-family: "etelkLight";
        font-size: 14px;
        font-weight: 100;
        line-height: 20px;
        text-align: left;
      }
    }
  }
  .contact {
    margin-top: 100px;

    .map {
      margin-top: 50px;
      margin-bottom: 50px;
    }
    .contact-info {
      text-align: center;
      h3 {
        font-size: 24px;
        font-weight: 700;
        font-family: "etelkBold";
        margin-bottom: 20px;
        text-align: center;
        color: #00467a;
      }
      a {
        text-decoration: none;
        font-size: 18px;
        font-family: "etelkLight";
        font-weight: 100;
        text-align: center;
        display: inline-block;
        color: black;
        &:hover {
          transition: all 500ms;
          color: #f49d37;
          transform: scale(1.1);
        }
      }
      .instagram {
        margin: 0px 15px;
      }
    }
  }
}
