header {
  width: 100%;
}
header .navbar {
  width: 100%;
  padding: 30px 0px;
  background: #00467a;
  margin-top: 30px;
}
header .navbar .logo {
  display: block;
  text-decoration: none;
  color: white;
  font-size: 25px;
  font-weight: 700;
  padding-bottom: 10px;
  padding-top: 10px;
}
header .navbar .logo:hover {
  color: #f49d37;
}
header .navbar .nav {
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  header .navbar .nav {
    display: none;
  }
}
header .navbar .nav li {
  margin: 0px 10px;
}
header .navbar .nav li .link {
  text-decoration: none;
  font-size: 18px;
  font-weight: 700;
  color: white;
  transition: all 100ms;
  display: block;
  padding: 10px 0px;
  overflow: hidden;
  position: relative;
}
@media (max-width: 768px) {
  header .navbar .nav li .link {
    font-size: 14px;
  }
}
header .navbar .nav li .link:hover {
  color: #f49d37;
}
header .navbar .nav li .link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(-3000px);
  display: block;
  width: 50%;
  height: 3px;
  background: #f49d37;
}
header .navbar .nav li .link::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  transform: translate(3000px);
  right: 0;
  width: 50%;
  height: 3px;
  background: #f49d37;
}
header .navbar .nav li:hover .link::after,
header .navbar .nav li:hover .link::before {
  transform: translate(0px);
  transition: transform 400ms;
  color: #f49d37;
}
header .navbar .close {
  display: none;
  font-size: 20px;
  font-weight: 700;
  padding: 5px 10px;
}
@media (max-width: 768px) {
  header .navbar .close {
    display: block;
    font-size: 14px;
    padding: 5px;
  }
}
header .search {
  margin: 50px 0px;
  text-align: center;
  padding: 25px 0px;
}
header .search input {
  width: 70%;
  padding: 10px;
  background: transparent;
  border: 3px solid #00467a;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 700;
  color: black;
}
header .search .searching {
  border: 3px solid #00467a;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 700;
  color: black;
  padding: 10px;
}
header .shopBag {
  text-align: right;
  position: relative;
}
header .shopBag a {
  position: fixed;
  right: 0px;
  font-size: 18px;
  color: #00467a;
  border: 3px solid #00467a;
  display: flex;
  flex-direction: column;
  padding: 30px 20px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-right: none;
  text-decoration: none;
  box-shadow: 10px 10px 10px black;
  z-index: 11;
}
header .shopBag a span {
  position: absolute;
  top: 13px;
  right: 6px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 50%;
  color: white;
  background: #00467a;
}
header .scroll-top {
  position: fixed;
  top: 600px;
  right: 100px;
  background: #00467a;
  padding: 10px;
  border-radius: 50%;
  display: none;
}
header .scroll-top a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  font-weight: 700;
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item {
  position: relative;
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text {
  position: absolute;
  width: 30%;
  height: 30%;
  background: rgba(0, 0, 0, 0.4);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 30px;
  border: 6px solid #00467a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text h2 {
  font-size: 30px;
  font-weight: 700;
  color: white;
  text-align: center;
  font-family: "etelkBold";
  margin-bottom: 20px;
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text .tel-carousel {
  display: inline-block;
  text-align: center;
  margin: auto;
  text-decoration: none;
  font-size: 25px;
  font-weight: 100;
  font-family: "etelkLight";
  color: #00467a;
  background: #f49d37;
  padding: 15px 25px;
  border-radius: 30px;
  transition: all 500ms;
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text .tel-carousel:hover {
  color: #00467a;
  transform: scale(1.1);
}
header .owl-carousel .owl-nav button {
  background: transparent;
  border: none;
  font-size: 60px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f49d37;
}
header .owl-carousel .owl-nav .owl-prev {
  position: absolute;
  top: 50%;
  left: 80px;
  transform: translateY(-50%);
}
header .owl-carousel .owl-nav .owl-next {
  position: absolute;
  top: 50%;
  right: 80px;
  transform: translateY(-50%);
}
header .owl-carousel .owl-dots {
  position: absolute;
  bottom: 10px;
  right: 43%;
  transform: translate(-50%, -50%);
}
header .owl-carousel .owl-dots .owl-dot {
  width: 20px;
  height: 20px;
  background: #fff;
  border: 2px solid #f49d37;
  margin: 0px 10px;
  border-radius: 50%;
}
header .owl-carousel .owl-dots .owl-dot.active {
  background: #f49d37;
}
header .owl-carousel img {
  width: 100%;
  height: 800px;
}
section {
