}
section .product .owl-carousel .owl-dot {
  display: none;
}
section .product .owl-carousel .owl-nav button {
  background: transparent;
  border: none;
  font-size: 70px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f49d37;
}
section .product .owl-carousel .owl-nav .owl-prev {
  position: absolute;
  top: 20%;
  left: -15%;
}
section .product .owl-carousel .owl-nav .owl-next {
