}
section .benefits {
  margin-top: 100px;
}
section .benefits .text {
  text-align: center;
  margin: 50px 0px;
}
section .benefits .text .img-icon {
  max-width: 100px;
  height: 100px;
  margin: auto;
}
section .benefits .text h3 {
  font-size: 22px;
  font-weight: 700;
  margin: 20px 0px;
  text-align: left;
  font-family: "etelkBold";
  color: #00467a;
}
section .benefits .text h3::after {
  content: "";
  display: block;
  margin-top: 10px;
  width: 20%;
  height: 4px;
  background: #00467a;
}
section .benefits .text p {
  font-family: "etelkLight";
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  text-align: left;
}
section .contact {
