}
section .contact .map {
  margin-top: 50px;
  margin-bottom: 50px;
}
section .contact .contact-info {
  text-align: center;
}
section .contact .contact-info h3 {
  font-size: 24px;
  font-weight: 700;
  font-family: "etelkBold";
  margin-bottom: 20px;
  text-align: center;
  color: #00467a;
}
section .contact .contact-info a {
  text-decoration: none;
  font-size: 18px;
  font-family: "etelkLight";
  font-weight: 100;
  text-align: center;
  display: inline-block;
  color: black;
}
section .contact .contact-info a:hover {
  transition: all 500ms;
  color: #f49d37;
  transform: scale(1.1);
}
section .contact .contact-info .instagram {
  margin: 0px 15px;
}
