@import "./params/mix";
@import "./header";
@import "./section";
@import "./footer";

*:hover {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  min-width: 320px;
  font-family: "etelkReg";
  // background: #fceff9;
}

.btn {
  border: none;
  background: transparent;
  color: #f49d37;
  border: 2px solid #f49d37;
  border-radius: 5px;
}

h2 {
  font-size: 35px;
  font-weight: 700;
  text-transform: capitalize;
  border-bottom: 6px solid #f49d37;
  color: #f49d37;
  padding: 15px 0px;
  margin-bottom: 50px;
  font-family: 'etelkBold';
}
